.cardList {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: -15px;
}

.movieCard {
    display: inline-flex;
    max-width: 200px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 15px;
    width: calc(100 / 2 * 30px) / 2;
}