.header {
    padding: 40px 50px;
    box-shadow: -13px 5px 20px 0px rgb(34 60 80 / 60%);
}

.link {
    text-decoration: none;
    border-radius: 15px;
    cursor: pointer;
    padding: 10px 15px;
    font-size: 25px;
    font-weight: 700;
    color: rgb(32, 31, 31);
    transition: color 300ms ease, background-color 300ms ease;
}

.link:first-child {
    margin-right: 10px;
}

.link:hover,
.link:focus {
    background-color: rgb(156, 99, 209);
    color: rgb(248, 248, 248);
}

.link.activeLink {
    background-color: rgb(156, 99, 209);
    color: rgb(248, 248, 248);
}